	background-color: #fff;
	z-index: 15;
	box-shadow: 0 0 30px rgba(0, 0, 0, 0.09);
	transition: all .3s ease;
}
.arlo_tm_leftpart.opened{left: -400px;}
.arlo_tm_leftpart.opened2{left: -400px;}
.arlo_tm_leftpart .inner{
	width: 100%;
	height: 100%;
	clear: both;
	float: left;
	padding: 60px;
}
.arlo_tm_leftpart .inner .logo{float: left;}
.arlo_tm_leftpart .inner .logo .light{display: none;}
.arlo_tm_leftpart .inner .logo img{
	max-width: 200px;
	max-height: 200px;
}
.arlo_tm_leftpart .inner .menu{
	width: 100%;
	height: auto;
	clear: both;
	float: left;
}
.arlo_tm_leftpart .inner .menu ul{
	margin: 0px;
	list-style-type: none;
	width: 100%;	
    min-height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.arlo_tm_leftpart .inner .menu ul li{margin: 0px;}
.arlo_tm_leftpart .inner .menu ul li a{
	text-decoration: none;
	color: #000;
	font-family: "Raleway";
	text-transform: capitalize;
	font-weight: 500;
	display: block;
	position: relative;
	padding: 13px 0px;
	transition: all .3s ease;
}
.arlo_tm_leftpart .inner .menu ul li a:hover{color: #297ac5;}
.arlo_tm_leftpart .inner .menu ul li a:before{
	position: absolute;
	content: "";
	width: 100%;
	height: 2px;
	background-color: rgba(0,0,0,.1);
	left: 0px;
	bottom: 0px;
}
.arlo_tm_leftpart .inner .menu ul li:last-child a:before{display: none;}
.arlo_tm_leftpart .inner .menu ul li a:after{
	position: absolute;
	content: "";
	width: 0%;
	height: 2px;
	background-color: rgba(0,0,0,.08);
	left: 0px;
	bottom: 0px;
	transition: all .3s ease;
}
.arlo_tm_leftpart .inner .menu ul li:last-child a:after{display: none;}
.arlo_tm_leftpart .inner .menu ul li a:hover:after{width: 100%;}
.arlo_tm_leftpart .inner .bottom{
	position: absolute;
	bottom: 30px;
	left: 60px;
}
.arlo_tm_leftpart .inner .bottom ul{
	margin: 0px;
	list-style-type: none;
}
.arlo_tm_leftpart .inner .bottom ul li{
	margin: 0px 10px 0px 0px;
	display: inline-block;
}
.arlo_tm_leftpart .inner .bottom ul li a{
	text-decoration: none;
	color: #000;
	transition: all .3s ease;
}
.arlo_tm_leftpart .inner .bottom ul li a:hover{color: #ff3651;}
.arlo_tm_leftpart .inner .resize{
	width: 38px;
	height: 38px;
	background-color: #29c565;
	position: absolute;
	right: 0px;
	bottom: 20%;
	transition: all .3s ease;
}
.arlo_tm_leftpart .inner .resize.opened{right: -38px;}
.arlo_tm_leftpart .inner .resize.opened:hover{background-color: #ff3651;}
.arlo_tm_leftpart .inner .resize:hover{background-color: #ff3651;}
.arlo_tm_leftpart .inner .resize a{
	position: absolute;
	top: 0px;
	bottom: 0px;
	left: 0px;
	right: 0px;
	z-index: 1;
}
.arlo_tm_leftpart .inner .resize a span{
    display: inline-block;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 5px 7px 5px 0;
    border-color: transparent #fff transparent transparent;
    position: absolute;
	top: 14px;
    left: 15px;
	-webkit-transform: rotate(0deg);
	        transform: rotate(0deg);
	transition: all .3s ease;
}
.arlo_tm_leftpart .inner .resize a span.opened{
	-webkit-transform: rotate(-180deg);
	        transform: rotate(-180deg);
	left: 16px;
}

